const Cite = require('citation-js')

function yearComparison(citA, citB) {
    if( citA.issued == undefined)
        return -1
    if( citB.issued == undefined)
        return 1
    return citA.issued['date-parts'][0][0] - citB.issued['date-parts'][0][0]
}

export function getBibliography(bibtext, format) {
    const pubs = new Cite(bibtext).format('data', {format: 'object'});
    console.log(pubs)
    pubs.sort(yearComparison)
    let references = []
    pubs.forEach( cite_obj => references.push(new Cite(cite_obj).format('bibliography', {template: format})) ) 
    return references;
}

