import ReactMarkdown from "react-markdown";
import "./header.css"

const Header = (props : {body : string, image: string}) => {
    let image = `url(${props.image})`;

    return <header style={{backgroundImage: image }}>
      <div>
      <ReactMarkdown>
        {props.body}
      </ReactMarkdown>
      </div>
    </header>
}

export default Header