import { BrowserRouter, Routes, Route } from "react-router-dom";
import TextPage from './pages/text-page';
import TeamPage from './pages/team-page';
import Navbar, { Tab } from './header/navbar';
import Footer from './footer/footer';
import ImagesPage from './pages/images-page';
import Header from './header/header';
import { PubsPage } from './pages/pubs-page';
import { useEffect, useState } from 'react';

const page_of = ( p : any) => {
  let title = p.title === undefined ? p.header : p.title;
  switch (p.type) {
    case "team":
      return <TeamPage title={title} team={p.members}/>
    case "text":
      return <TextPage title={title} body={text_of(p.body)}/>
    case "images": 
      let pars = p.paragraphs.map( (pp: { text: string | string[]; image: any; }) => {
        return {
          "text" : text_of(pp.text),
          "image" : pp.image
        }
      }  )

      return <ImagesPage title={title} paragraphs={pars}/>
    case "pub" : 
      return <PubsPage title={title} bib={p.bib} format={p.format === undefined ? p.format : "apa"}/>
    default:
      return <TextPage title="Page incorrectly defined" body=""/>
  }
}





function text_of( text : string | string[]) {
    if(typeof text == "string")
      return text;
    else 
      return text.join("\n\n");
} 

function App() {

  const [isBusy, setBusy] = useState(true)
  const [config, setConfig] = useState<any>(undefined)

  useEffect( () => {
    if(isBusy)
      fetch("/config.json")
      .then( (response) => response.json() )
      .then( (json) => {
        console.log(json)
        setConfig(json);
        setBusy(false);
      })  })

  if(config === undefined && isBusy)
    return <></>
    

  let home_tab : Tab = { header : config.home.header, path : "/" };
  let tabs : Tab[] = config.pages.map( (p: { header: any; path: any; }) => {return { header: p.header, path : p.path } });
  tabs.unshift(home_tab);


  return (
    <>
    <BrowserRouter>
      <Header body={text_of(config.header.text)} image={config.header.image}/>
      <Navbar tabs={tabs}/>
      <Routes>
          <Route index element={<TextPage title={config.home.title} body={text_of(config.home.body)}/>} />
          {config.pages.map( (p: { path: string | undefined; }) => 
            <Route path={p.path} index element={page_of(p)} /> )}
      </Routes>
      <Footer tabs={tabs} footer_notes={config.footer}/>
    </BrowserRouter></>
  );
}

export default App;
