import "./pubs-page.css"
import { useState } from "react";
import { getBibliography } from "../publications";

async function getPubsString(bibfile : string) {

    let filename = bibfile;

    if(!bibfile.endsWith(".bib"))
      filename += ".bib"

    return await fetch(filename)
    .then( (response) =>  { return response.text() } );
}


const PubsPage = (props : { title : string, bib: string, format : string } ) => {

  const [bib, setBib] = useState<any[] | undefined>(undefined)

  if(bib === undefined)
    getPubsString(props.bib).then( (text) => {
      

      setBib(getBibliography(text, props.format))
      
    });


  return  <div className="body-block">
  <h1>{props.title}</h1>
    <ul className="pubs">
      {bib === undefined ? <></> : bib.map( (ref) => {
          return <li>{ref}</li>;
      } )}
    </ul>
</div>

}

export { PubsPage };