import { ReactMarkdown } from "react-markdown/lib/react-markdown"
import { NavLink } from "react-router-dom"
import { Tab } from "../header/navbar"
import "./footer.css"

interface FooterNote { title : string, lines : string[]}


const Footer = (props : { tabs : Tab[], footer_notes : FooterNote[] }) => {
    return <footer>
    <div>
      <h5>Pages</h5>
      {props.tabs.map( (tab) => <NavLink to={tab.path}>{tab.header}</NavLink>)}
    </div>
    { props.footer_notes.map( (note) => <div>
      <h5>{note.title}</h5>
      {note.lines.map ( (ct) => <ReactMarkdown>{ct}</ReactMarkdown>)}
    </div>  )
  }
    </footer>
}

export default Footer;